@import "../../variables.scss";

.main--text--wrap{
  width: 100%;
  text-align: center;
  color: #fff;
  font-family: $textFont;
  font-size: 15px;
  position: absolute;
  top: 20px;
}

.main--text--h1{
  font-size: 25px;
  margin-bottom: 10px;
}

.chart--common{
  position: absolute;
}

.modelNav--wrap {
  -webkit-animation: slideInLeft 0.4s;
  -moz-animation: slideInLeft 0.4s;
  -ms-animation: slideInLeft 0.4s;
  animation: slideInLeft 0.4s;
  width: 200px;
  position: fixed;
  left: 50%;
  top: 60px;
  margin-left: -740px;
  z-index: 1;
  background-color: $mainColor;
  color: #fff;
  padding: 5px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.modelNav--div {
  max-height: fit-content;
}

.modelNav--input--wrap {
  width: 100%;
}

.model--params--form {
  margin-top: 5px;
}

.model--param--submit {
  width: 100%;
  text-align: center;
  line-height: 24px;
  background-color: #00a178;
  border-radius: 4px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  border: none;
  font-family: $textFont;

  &:hover {
    background-color: #008261;
  }
}
