@charset "UTF-8";
@import "variables.scss";

/* CSS Document */
/* Let's get this party started */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  // background: #fff;
  // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0);
  // -webkit-border-radius: 8px;
  // border-radius: 8px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  // -webkit-border-radius: 8px;
  border-radius: 4px;
  background: #333;
  // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

//
// ::-webkit-scrollbar-thumb:window-inactive {
//   background: rgba(51,51,51,0.6);
// }

html,
body {
  height: 100%;
  background-color: $mainColor;
}

body,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
li,
ol,
p,
ul {
  margin: 0;
  padding: 0;
}

body {
  padding: 0 !important;
  // position: absolute;
  // width: 100%;
  // height: 100%;
  // background-color: #111;
  // color: #fff;
}

body,
button,
h1,
h2,
h3,
h4,
h5,
h6,
input,
td,
th {
  //font: 12px "돋움", dotum, Arial, sans-serif;
  font-size: 12px;
}

li,
ol,
ul {
  list-style: none;
}

fieldset,
img {
  border: none;
  vertical-align: middle;
}

legend {
  display: none;
}

a {
  text-decoration: none;

  &:ACTIVE,
  &:VISITED {
    text-decoration: none;
  }
}

address {
  font-style: normal;
}

:-moz-placeholder,
:-ms-input-placeholder,
::-moz-placeholder,
::-webkit-input-placeholder {
  text-transform: initial !important;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none !important;
}

input:focus {
  // text-transform: lowercase;
  // border: none!important;
  border-right: 3px solid #111 !important;
}

select {
  /*for firefox*/
  -moz-appearance: none;
  /*for chrome*/
  -webkit-appearance: none;
}

/*for IE10*/
select::-ms-expand {
  display: none;
}

/* common class */
body {
  overflow: hidden !important;
  cursor: default !important;
}

/* This used to work for parent element of button divs */
/* But it is not enough now, below dont hide play button parent div */
video::-webkit-media-controls {
  display: none !important;
}

video::-webkit-media-controls-enclosure {
  display: none !important;
}

*::-webkit-media-controls-panel {
  display: none !important;
  -webkit-appearance: none;
}

/* Old shadow dom for play button */
*::--webkit-media-controls-play-button {
  display: none !important;
  -webkit-appearance: none;
}

/* New shadow dom for play button */
/* This one works */
*::-webkit-media-controls-start-playback-button {
  display: none !important;
  -webkit-appearance: none;
}

input {
  border: none !important;
}


.screen_out {
  text-indent: -9999px;
  overflow: hidden;
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  z-index: -1;
}

.clearfix:after {
  content: '.';
  display: block;
  width: 0;
  height: 0;
  overflow: hidden;
  text-indent: -9999px;
  clear: both;
}

.pageLink {
  display: block;
  text-decoration: underline;
}

.title--common--h1 {
  text-align: left;
  font-family: $textFont;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 20px;
}

.subTitle--common-h1 {
  font-size: 20px;
  margin-bottom: 0px;
}

.fadeEnterAndLeave {
  // @include normal_transition(0.3s);
}

.fadeEnterAndLeave.ng-enter {
  animation: 0.5s fadeIn;
}

.fadeEnterAndLeave.ng-leave {
  animation: 0.5s fadeOut;
}

.scrollbar--666::-webkit-scrollbar-thumb {
  background: #666;
}

.scrollbar--eee::-webkit-scrollbar-thumb {
  background: #eee;
}

.common--basic--wrap {
  width: 1480px;
  height: 750px;
  background-color: #f7f7f7;
  position: relative;
  top: 50px;
  left: 50%;
  margin-left: -740px;
  border-radius: 4px;
  overflow-y: scroll;
}

.highcharts-credits {
  display: none !important;
}

.displayNone {
  display: none !important;
}
